import * as React from "react";
import { Button, ButtonBase, Grid, TextField } from "@material-ui/core";
import { primaryColor } from "../../components/style";
// @ts-ignore
import logo from '../../images/Logo.png';
import axios from '../../components/ajax';
import { Link, navigate } from 'gatsby';
import Defer from "../../components/ui/Defer";
import save from "../../components/save";

const Page = () => {
    const [load, setLoading] = React.useState(false);
    /**
     * @type {React.MutableRefObject<any>}
     */
    const form = React.useRef();
    const submit = (e) => {
        e.preventDefault();
        setLoading(true);
        var payload = {
            "name": form.current.name.value,
            "phone": form.current.phone.value,
            "nik": form.current.nik.value,
            "password": form.current.password.value
        };
        axios.post('/auth/register', payload).then(x => {
            save.token = x.data.token;
            localStorage.setItem('token', x.data.token);
            localStorage.setItem('user', payload.phone);
            localStorage.setItem('pass', payload.password);
            navigate('/user/');
        }).catch(x => {
            alert(x.response?.data.message || x.message);
            setLoading(false);
        });
    }

    return (
        <div>
            <Defer data={!load}>
                {() => <form onSubmit={submit} ref={form}>
                    <Grid container spacing={0}
                        style={{ minHeight: '100vh', backgroundColor: primaryColor }}
                        direction="column"
                        alignItems="center"
                        justifyContent="center">
                        <Grid item>
                            <img src={logo} width="200px" alt="" style={{ marginBottom: '1em' }} />
                        </Grid>
                        <Grid item>
                            <TextField name="name" autoComplete="name" label="Nama" variant="filled" />
                        </Grid>
                        <Grid item>
                            <TextField name="nik" autoComplete="number" label="NIK" variant="filled" />
                        </Grid>
                        <Grid item>
                            <TextField name="phone" autoComplete="tel" label="No. HP" variant="filled" />
                        </Grid>
                        <Grid item>
                            <TextField name="password" type="password" autoComplete="new-password" label="Password" variant="filled" />
                        </Grid>
                        <Grid item style={{ margin: '0.5em' }}>
                            <Button variant="contained" type="submit">
                                Daftar
                            </Button>
                        </Grid>
                        <Grid item>
                            <Button component={Link} to="/user/login/">
                                Kembali
                            </Button>
                        </Grid>
                    </Grid>
                </form>
                }
            </Defer>
        </div>
    )
}

export default Page;
